import './SignUp.scss';
import Swal from 'sweetalert2';
import { useState } from 'react'; //useEffect
import { ReactComponent as LogoIcon } from 'assets/images/ac-logo.svg';

// API
import { register } from 'api/auth';
import { useNavigate } from 'react-router-dom';

// 
// const emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;

export const TopIcon = ({ title }) => {
  return (
    <>
      <div className="iconBox">
        <LogoIcon className="icon" />
      </div>
      <h2 className="iconTitle">{title}</h2>
    </>
  );
};

export const AuthInput = ({
  id,
  name,
  type,
  value,
  label,
  placeholder,
  onChange,
  maxLength,
}) => {
  const minLength = 4;

  return (
    <div className="formContainer">
      <div className="group">
        <label htmlFor={id}>
          {label}
          <span className="mustInput">*</span>
        </label>
        <input
          id={id}
          name={name}
          type={type}
          value={value}
          placeholder={placeholder}
          onChange={(event) => onChange?.(event.target.value)}
          maxLength={maxLength}
        />
        <div className="alerGroup">
          {value ? (
            (value.length < minLength || value.length >= maxLength) && (
              <div className="alertBox">
                <span className="alert">
                  {minLength}~{maxLength}
                </span>
              </div>
            )
          ) : (
            <div></div>
          )}
          <div className="numAlertBox">
            <span className="lengthNum">{value ? value.length : '0'}</span>/
            <span className="fityNum">{maxLength}</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export const OrangeBtn = ({ way, onClick }) => {
  return (
    <button className="orange" onClick={onClick}>
      {way}
    </button>
  );
};

export const SignUp = () => {

  // input
  const [account, setAccount] = useState('');
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [checkPassword, setCheckPassword] = useState('');
  const navigate = useNavigate

  // 
  const onFormSubmit = async (e) => {
    e.preventDefault();

    // 
    if (
      account.trim().length === 0 ||
      name.trim().length === 0 ||
      email.trim().length === 0 ||
      password.trim().length === 0
    ) {
      Swal.fire({
        icon: 'error',
        title: '',
        text: '',
      });

      return;
    }

    // 
    if (password !== checkPassword) {
      Swal.fire({
        icon: 'error',
        title: '',
        text: '',
      });

      return;
    }

    try {
      const response = await register({
        account,
        name,
        email,
        password,
        checkPassword,
      });

      // 
      // console.log('');
      // console.log(response);

      if (response.data.status === 'success') {
        Swal.fire({
          position: 'top',
          title: '',
          timer: 1000,
          icon: 'success',
          showConfirmButton: false,
        });
        
        navigate('/login')
        return
      }

    } catch (error) {

      // 
      // console.log('');

      // account
      if (error.response.data.data['Error Message'] === 'account used') {
        Swal.fire({
          icon: 'error',
          title: '',
          text: '',
        });
        return;
      }

      // email
      if (error.response.data.data['Error Message'] === 'email used') {
        Swal.fire({
          icon: 'error',
          title: '',
          text: 'email',
        });
        return;
      }

      // 
      // console.log(error.response.data.data['Error Message'])
    }

  };

  return (
    <div className="SignUpcontainer">
      <TopIcon title="" />

      {/* .map */}
      <form>
        <AuthInput
          type="text"
          id="account"
          name="account"
          label=""
          value={account}
          placeholder=""
          onChange={(accountInputValue) => {
            setAccount(accountInputValue);
          }}
          maxLength="20"
        />
        <AuthInput
          type="text"
          id="name"
          name="name"
          label=""
          value={name}
          placeholder=""
          onChange={(nameInputValue) => {
            setName(nameInputValue);
          }}
          maxLength="20"
        />
        <AuthInput
          type="text"
          id="email"
          name="email"
          label="Email"
          value={email}
          placeholder="Email"
          onChange={(emailInputValue) => {
            setEmail(emailInputValue);
          }}
          maxLength="50"
        />
        <AuthInput
          type="password"
          id="password"
          name="password"
          label=""
          value={password}
          placeholder=""
          onChange={(passwordInputValue) => setPassword(passwordInputValue)}
          maxLength="20"
        />
        <AuthInput
          type="password"
          id="password-2"
          name="password-2"
          label=""
          value={checkPassword}
          placeholder=""
          onChange={(passwordAgnInputValue) =>
            setCheckPassword(passwordAgnInputValue)
          }
          maxLength="20"
        />
      </form>

      <div className="btnGroup">
        <OrangeBtn way="" onClick={onFormSubmit} />
        <div className="aLink">
          <a href="/login" className="cancel-link">
            
          </a>
        </div>
      </div>
    </div>
  );
};
