import './PostModal.scss'

import { useState } from 'react'

//import img
import avatarDefulat from 'assets/images/default_avatar.svg'

export const PostModal = () => {
  const [inputValue, setInputValue] = useState("")

  return (
    <div className="modal fade" id="postModal" tabIndex="-1" aria-labelledby="postModalLabel" aria-hidden="true">
      <div className="modal-dialog">
        <div className="modal-content">
          <div className="modal-header">
            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div className="modal-body">
            <div className="postModalContianer">
              <img className="modalAvatarImg" src={avatarDefulat} alt="" />
              <textarea
                id='tweet-textarea'
                className='tweetInput'
                type='text'
                rows={13} 
                cols={50}
                value={inputValue || ''}
                placeholder='?'
                maxLength={140}
                onChange={(e) => setInputValue(e.target.value)}
              >
              </textarea>
            </div>                
          </div>
          <div className="modal-footer">
            {/*  */}
            {inputValue.length === 0 &&
              <div className="alertWord">
                
              </div>
            }
            {/* 140 */}
            { inputValue.length >= 140 && 
              <div className="alertWord">
                 140 
              </div>
            }
            <button className='modalButton' ></button>
          </div>
        </div>
      </div>
    </div>
  )
}